$mobile: 320px;
$tablet: 768px;
$desktop: 1060px;

//All Devices

.header {
	background-image: url("../../assets/background_header.svg");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;

	.nav-main {
		display: flex;
		justify-content: start;
		justify-content: space-between;
		padding: 10px 60px 10px;


		.nav-left {
			justify-self: start;
			margin-left: 30px;
		}

		.nav-right {
			justify-self: end;
			display:flex;
			align-items: center;
			justify-content: center;

			a{
				font-weight: 400;
				font-size: 16px;
				line-height:20.8px;
				text-align: center;
				text-decoration: none;
				padding: 20px;
				color: rgba(250, 250, 255, 0.8);
			}
		}
	}

	.main {
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;

		.left-section {
			color: #ecebf5;
			display: flex;
			flex-direction: column;

			img {
				width: 150px;
				height: 48px;
				border-radius: 8px;
			}
		}

		.right-section {
			display: flex;
			justify-content: center;
			align-content: center;
		}
	}
}

@media screen and (min-width: $mobile) {
	.main {
		min-height: 786px;
		flex-direction: column;

		.left-section {
			align-items: center;
			text-align: center;

			h1 {
				font-size: 28px;
				font-style: normal;
				font-weight: 600;
				line-height: 39.2px;
				margin-top: 40px;
				margin-bottom: 0px;
				margin-right: 0px;
			}

			p {
				width: 290px;
				color: rgba(250, 250, 255, 0.8);
			}

			.download-app {
				display: flex;
				align-items: center;

				img {
					padding: 2px;
				}
			}
		}

		.right-section {
			.device2 {
				margin-top: 200px;
				margin-left: 10px;
				margin-right: -70px;
			}

			.device1 {
				margin-top: 80px;
			}

			img {
				width: 182.19px;
				height: 325.22px;
			}
		}
	}
}

@media screen and (min-width: $tablet) {
	.main {
		min-height: 1091px;
		flex-direction: column;

		.left-section {
			align-items: center;
			text-align: center;
			width: 70%;

			h1 {
				font-size: 48px;
				font-style: normal;
				font-weight: 600;
				line-height: 67.2px;
			}

			p {
				width: 512px;
				color: rgba(250, 250, 255, 0.8);
			}

			.download-app {
				display: flex;
				align-items: center;

				img {
					margin: 10px;
				}
			}
		}

		.right-section {
			.device2 {
				margin-top: 250px;
				margin-right: -85px;
			}

			.device1 {
				margin-top: 80px;
			}

			img {
				width: 265.09px;
				height: 473.2px;
			}
		}
	}
}

@media screen and (min-width: $desktop) {
	.main {
		min-height: 1100px;
		flex-direction: row;

		.left-section {
			flex-direction: column;
			text-align: left;
			align-items: flex-start;
			width: fit-content;

			h1 {
				width: 592px;
				font-size: 48px;
				font-style: normal;
				font-weight: 600;
				line-height: 67.2px;
			}

			p {
				width: 488px;
				color: rgba(250, 250, 255, 0.8);
			}

			img {
				width: 150px;
				height: 48px;
				border-radius: 8px;
			}
		}

		.right-section {
			.device2 {
				margin-top: 310px;
				margin-left: 0px;
				margin-right: -95px;
			}

			.device1 {
				margin-right: 0px;
				margin-left: -20px;
				margin-top: 115px;
			}

			img {
				width: 265.09px;
				height: 473.2px;
			}
		}
	}
}
